header{
    border-bottom: .22rem solid var(--color-1);
    display: flex;
    flex-direction: column;
    position: relative;
}

.freeShipping__bar{
    background-color: var(--color-3);
    border-bottom: .20rem solid var(--color-1);
    font-size: .9rem;
    font-weight: 600;
    letter-spacing: 1px;
    padding: 1rem;
    text-align: center;
}

.logo{
    transform: scale(.8);
    transition: .3s;
}

.header__top{
    align-items: center;
    background-color: var(--color-2);
    display: flex;
    justify-content: space-between;
    padding: 0rem 1rem;
    position: relative;
}

.dialog{
    background-color: rgba(0, 0, 0, 0.7);
    height: 100dvh;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 100;
}

nav{
    align-items: flex-end;
    background-color: var(--color-3);
    border-right: .4rem solid var(--color-1);
    display: flex;
    flex-direction: column;
    gap: 3rem;
    height: 100%;
    padding: 5.5rem 2rem;
    position: relative;
    width: min(90dvw, calc(100% - 3rem));
}

.close__btn{
    cursor: pointer;
    transform: scale(1.2);
    transition: .3s;
}

.close__btn:hover{
    transform: rotate(-4deg) scale(1.05);
}

.close__btn:active{
    transform: rotate(-184deg) scale(.98);
}

ul.menu{
    align-items: flex-end;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    list-style: none;
}

.menu a{
    color: var(--color-1);
    font-size: 1.6rem;
    font-weight: 500;
    letter-spacing: 1px;
    text-decoration: none;
}

.menu a:hover, .menu a:active{
    color: var(--color-4);
    text-decoration: underline;
}

.search__container{
    align-items: flex-end;
    display: flex;
    gap: .5rem;
    justify-content: center;
}

.search__dialog{
    align-items: center;
    background-color: var(--color-4);
    border: .14rem solid var(--color-1);
    border-radius: 2rem 0 0 2rem;
    box-shadow: .2rem .2rem 0 0 var(--color-1);
    display: flex;
    gap: .35rem;
    justify-content: center;
    max-width: 640px;
    padding: 1rem 2rem;
    position: absolute;
    right: -.5rem;
    top: 6.5rem;
    width: 75dvw;
    z-index: 0;
}


.search__container input{
    background-color: var(--color-2);
    border: .14rem solid var(--color-1);
    border-radius: .4rem;
    box-shadow: .14rem .14rem 0 0 var(--color-1);
    font-size: 1.1rem;
    font-weight: 300;
    letter-spacing: 1px;
    opacity: .8;
    outline: none;
    padding: .3rem;   
    transition: .2s;
    width: 100%;
}

.search__container input:hover{
    opacity: 1;
}

.search__container input:focus{
    filter: brightness(1.1);
    opacity: 1;
}

.header__btns__container{
    display: flex;
    gap: .4rem;
}

.menu__btn{
    background-color: var(--color-4);
    border: .16rem solid var(--color-1);
    border-radius: .4rem;
    box-shadow: .16rem .16rem 0 0 var(--color-1);
    cursor: pointer;
    display: flex;
    padding: .4rem;
    transform: scale(.8);
}

.menu__btn:active{
    box-shadow: .1rem .1rem 0 0 var(--color-1);
    filter: brightness(.9);
    transform: rotate(8deg) scale(.85);
}