.products__container{
    align-items: center;
    display: grid;
    gap: 4rem;
    grid-template-columns: 1fr;
    justify-content: center;
    max-width: 1280px;
    justify-content: center;
}

.product{
    background-color: var(--color-2);
    border: .2rem solid var(--color-1);
    border-radius: .6rem;
    box-shadow: .6rem .6rem 0 0 var(--color-1);
    display: flex;
    flex-direction: column;
    height: max-content;
    max-width: 600px;
    transition: .3s;
}

.product:hover{
    box-shadow: .4rem .4rem 0 0 var(--color-1);
    transform: scale(.96);
}

.product:active{
    box-shadow: .2rem .2rem 0 0 var(--color-1);
    transform: scale(.92);
}

.product a{
    text-decoration: none;
}

.product h2{
    background-color: var(--color-1);
    color: var(--color-2);
    font-size: 1.8rem;
    letter-spacing: 2px;
    padding: 1rem;
    text-align: center;
    width: 100%;
}

.description__container{
    max-width: 440px;
    padding: 1rem;
}

.product img{
    max-height: 240px;
    max-width: 100%;
    object-fit: cover;
    width: 100%;
}

.product p{
    color: var(--color-1);
    font-size: 1rem;
    line-height: 1.5;
    padding-bottom: 2rem;
}

.product span{
    color: var(--color-1);
    font-size: .95rem;
    font-weight: 600;
    letter-spacing: 1px;
}