@media screen and (min-width: 768px) {

    .header__top{
        padding: 1rem 2rem;
    }


    .logo{
        transform: scale(1);
    }

    .logo:hover{
        filter: brightness(1.05);
        transform: scale(.96);
    }

    .cta__btn:hover{
        box-shadow: .1rem .1rem 0 0 var(--color-1);
        filter: brightness(1.05);
        transform: rotate(-1deg) scale(.96);
    }

    .logo:active, .cta__btn:active{
        filter: brightness(.98);
    }

    .header__btns__container{
        display: flex;
        gap: .6rem;
    }
    
    .menu__btn{
        border: .16rem solid var(--color-1);
        border-radius: .4rem;
        box-shadow: .16rem .16rem 0 0 var(--color-1);
        cursor: pointer;
        display: flex;
        padding: .4rem;
        transform: scale(.9);
        transition: .3s;
    }

    .menu__btn:hover{
        box-shadow: .1rem .1rem 0 0 var(--color-1);
        filter: brightness(1.1);
        transform: rotate(4deg) scale(.85);
    }

    .cart:hover{
        transform: rotate(-4deg) scale(.85);
    }

    .menu__btn:active{
        filter: brightness(.9);
    }

    .banner{
        align-items: center;
        display: grid;
        grid-template-columns: 1fr 30%;
        max-width: 1280px;
        padding: 2rem;
    }

    .banner img{
        max-width: 100%;
        width: 100%;
    }

    .banner p{
        font-size: 1.4rem;
    }

    .products__container{
        grid-template-columns: repeat(2, 1fr);
    }

    .card__container{
        grid-template-columns: repeat(2, 1fr);
    }

    .search__dialog{
        top: 7.5rem;
    }
}

@media screen and (min-width: 1024px) {

    .header__top{
        position: relative;
    }
    .dialog{
        background-color: transparent;
        display: flex !important;
        height: 0;
        position: relative;
    }

    nav{
        align-items: flex-start;
        background-color: transparent;
        border: none;
        height: 0;
        justify-content: center;
        padding: 0 2rem;
        width: fit-content;
    }

    ul.menu{
        align-items: flex-start;
        flex-direction: row;
    }

    .close__btn{
        display: none;
    }

    .menuBtn{
        display: none;
    }

    .products__container{
        grid-template-columns: repeat(3, 1fr);
    }

    .product{
        max-width: 440px;
    }

    .card__container{
        grid-template-columns: repeat(3, 1fr);
    }
}