.banner{
    align-self: center;
    display: flex;
    flex-direction: column-reverse;
    transition: 1s;
    z-index: 1;
}

.banner section{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 2rem;
}

h1{
    font-size: 1.8rem;
    font-weight: 800;
    letter-spacing: 1px;
}

.banner p{
    font-size: 1.2rem;
    line-height: 1.5;
    max-width: 600px;
}

.cta__btn{
    background: var(--color-4);
    border: .1rem solid var(--color-1);
    border-radius: .4rem;
    box-shadow: .2rem .2rem 0 0 var(--color-1);
    color: var(--color-1);
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: 400;
    letter-spacing: 1px;
    padding: 1rem 2rem;
    text-decoration: none;
    transition: .2s;
    width: fit-content;
}

.cta__btn:active{
    box-shadow: .1rem .1rem 0 0 var(--color-1);
    filter: brightness(.98);
    filter: brightness(1.05);
    transform: rotate(-1deg) scale(.96);
}

.banner picture{
    display: flex;
    justify-content: center;
}

.banner img{
    max-width: 55%;
    width: 100%;
}