footer{
    align-items: center;
    background-color: var(--color-1);
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 3rem;
}

footer a{
    align-items: center;
    display: flex;
    gap: .5rem;
    justify-content: center;
    flex-direction: column;
    text-decoration: none;
}

footer p{
    color: var(--color-4);
    font-size: 1.1rem;
    font-weight: 700;
    letter-spacing: 2px;
    text-align: center;
}
