.blog-title{
    font-size: 2.4rem;
    font-weight: 400;
    text-transform: uppercase;
}

.toggleView__btn{
    align-self: center;
    background-color: var(--color-4);
    border: .14rem solid var(--color-1);
    border-radius: .4rem;
    box-shadow: .2rem .2rem 0 0 var(--color-1);
    color: var(--color-1);
    cursor: pointer;
    font-size: 1.1rem;
    font-weight: 600;
    letter-spacing: 1px;
    padding: .75rem 1.5rem;
    transition: .2s;
    max-width: 640px;
    width: 80%;
}

.toggleView__btn:hover{
    transform: scale(.96);
}

.toggleView__btn:active{
    box-shadow: .1rem .1rem 0 0 var(--color-1);
    opacity: .85;
    transform: scale(.9);
}

.card__container{
    align-items: center;
    display: grid;
    gap: 4rem;
    grid-template-columns: 1fr;
    justify-items: center;
    max-width: 1280px;
    width: 100%;
}

.article__card{
    background-color: white;
    border: .2rem solid var(--color-1);
    border-radius: .6rem;
    box-shadow: .2rem .2rem 0 0 var(--color-1);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 768px;
    padding: 1rem;
}

.article__card div{
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.article__card h2{
    align-items: center;
    background-color: var(--color-1);
    display: flex;
    height: 75px;
    color: var(--color-2);
    justify-content: center;
    letter-spacing: 1px;
    padding: .5rem 1rem;
    text-align: center;
    width: 100%;
}

.article__card img{
    max-height: 440px;
    max-width: 100%;
    width: 100%;
}

.article__card p{
    line-height: 1.5;
    max-width: 540px;
    padding-bottom: 1rem;
}

.article__btn{
    background-color: var(--color-4);
    border: .2rem solid var(--color-1);
    border-radius: .6rem;
    box-shadow: .25rem .25rem 0 0 var(--color-1);
    color: var(--color-1);
    cursor: pointer;
    font-size: 1.1rem;
    letter-spacing: 1px;
    outline: none;
    padding: .5rem 1.5rem;
    transition: .2s;
    width: 100%;
}

.article__btn:hover{
    filter: brightness(1.1);
    transform: scale(.96);
}

.article__btn:active{
    box-shadow: .1rem .1rem 0 0 var(--color-1);
    opacity: .85;
    transform: scale(.9);
}

.article__dialog{
    background-color: rgba(0, 0, 0, 0.6);
    display: flex;
    height: 100dvh;
    justify-content: center;
    left: 0;
    padding: 1.5rem;
    position: fixed;
    top: 0;
    width: 100dvw;
    z-index: 100;
}

.article__dialog section{
    align-items: center;
    background-color: var(--color-2);
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    height: 100%;
    max-width: 1024px;
    overflow: scroll;
    position: relative;
}

.article__dialog img{
    border-radius: .4rem;
    box-shadow: 0 0 .6rem .1rem var(--color-1);
    max-height: 640px;
    max-width: 100%;
    object-fit: cover;
    width: 100%;
}

.article__dialog picture{
    background-color: var(--color-3);
    padding: 4rem;
    width: 100%;
}

.article__dialog h3{
    font-size: 1.8rem;
    padding: 1rem 0;
    text-transform: capitalize;
}

.article__dialog p{
    align-self: center;
    font-size: 1rem;
    line-height: 1.5;
    max-width: 640px;
    padding: 2rem;
    text-align: justify;
}

.article__dialog h4{
    font-size: 1.2rem;
    font-weight: 600;
    letter-spacing: 2px;
    padding-bottom: .4rem;
}

.article__dialog h5{
    font-size: 1rem;
    font-weight: 400;
    letter-spacing: 2px;
    padding-bottom: 1rem;
}

.article__dialog h6{
    font-size: 1rem;
    letter-spacing: 1px;
    padding-bottom: 2rem;
}

.article__dialog span{
    align-self: flex-start;
    font-size: 1rem;
    letter-spacing: 2.2px;
    padding: 2rem 4rem;
}

i.close__btn{
    position: absolute;
    right: .8rem;
    top: .8rem;
    z-index: 10;
}

.close__btn img{
    box-shadow: none;
    transform: scale(.8);
    transition: .3s;
}

.displayChange{
    align-self: center;
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    justify-content: center;
    max-width: 1024px;
}