@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import 'header.css';
@import 'footer.css';
@import 'banner.css';
@import 'blog.css';
@import 'products.css';
@import 'mediasQueries.css';


*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

:root{
    --color-1: #071E22;
    --color-2: #FBFEFB;
    --color-3: #AFD2E9;
    --color-4: #F3A712;
    --main-font: "Montserrat", sans-serif;
}

body{
    font-family: var(--main-font);
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    position: relative;
}

ul{
    list-style: none;
}

main{
    align-items: center;
    background-color: var(--color-3);
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    gap: 3rem;
    justify-content: center;
    padding: 3rem 2rem;
}

.hidden{
    display: none;
}

@keyframes slideInFromBottom{
    from{
        transform: translateY(100%);
    }
    to{
        transform: translateY(0);
    }
}

.open{
    animation: slideInFromBottom .4s forwards;
    transition: .3s;    
}

@keyframes slideInFromRightSide{
    from{
        transform: translateX(100%);
    }
    to{
        transform: translateX(0);
    }
}

.openFromRight{
    animation: slideInFromRightSide .4s forwards;
    transition: .3s;    
}

@keyframes reduceBannerHeight{
    20%{
        opacity: 0;
    }
    
    40%{
        height: 500px;
    }

    60%{
        height: 480px;
    }
    
    80%{
        height: 240px;
    }
    
    100%{
        opacity: 0;
        height: 120px;
        visibility: hidden;
    }
}

.reduceBanner{
    animation: reduceBannerHeight .2s forwards;
}